.sendForm {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.textarea {
    flex-grow: 8;
    padding: 10px;
    /*width: 100%;*/
    height: 40px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    overflow: hidden;
    resize: none;
    font-size: 14px;
    line-height: 17px;
}

.button {
    flex-grow: 1;
    margin-left: 44px;
    /*width: 120px;*/
    height: 40px;
    background: #0066CC;
    border-radius: 20px;
    text-align: center;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

