.friendMessage {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    left: 1162px;
    top: 204px;
}

.friendText {
    padding: 7px 13px 7px 25px;
    border-radius: 12px 12px 12px 0;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {

}

.friendName {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.friendMessageText {
    max-width: 530px;
    white-space: pre-wrap;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.friendTime {
    height: 12px;
    margin-top: 5px;
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    padding-left: 10px;
}
